// src/components/dashboard/HistorySection.tsx
"use client";

import React, { useEffect, useState } from "react";
import { Table, Spinner } from "react-bootstrap";
import { Edit3 } from "react-feather";

interface HistoryItem {
  id: string;
  date: string;
  serviceName: string;
  therapistName: string;
}

export default function HistorySection() {
  const [rows, setRows] = useState<HistoryItem[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch("/api/appointments/history")
      .then((res) => res.json())
      .then((data: HistoryItem[]) => setRows(data))
      .catch(console.error)
      .finally(() => setLoading(false));
  }, []);

  if (loading) {
    return (
      <div className="text-center py-5">
        <Spinner animation="border" />
      </div>
    );
  }

  return (
    <div className="overflow-auto">
      <Table hover responsive className="dashboard-table">
        <thead>
          <tr>
            <th>Servicio</th>
            <th>Terapeuta</th>
            <th>Fecha y hora</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {rows.length === 0 ? (
            // fila vacía para preservar estructura
            <tr>
              <td colSpan={4} className="text-center">
                — No hay sesiones agendadas —
              </td>
            </tr>
          ) : (
            rows.map((r) => {
              const dt = new Date(r.date);
              const fecha = dt.toLocaleDateString();
              const hora = dt.toLocaleTimeString([], {
                hour: "2-digit",
                minute: "2-digit",
              });
              const isFuture = dt.getTime() > Date.now();

              return (
                <tr key={r.id}>
                  <td>{r.serviceName}</td>
                  <td>{r.therapistName}</td>
                  <td>
                    {fecha} • {hora}
                  </td>
                  <td>
                    <span
                      title={
                        isFuture
                          ? "Editar esta sesión"
                          : "No puedes editar citas pasadas"
                      }
                      style={{ cursor: isFuture ? "pointer" : "not-allowed" }}
                    >
                      <Edit3
                        size={18}
                        className={isFuture ? "icon-editable" : "icon-disabled"}
                        onClick={() => {
                          if (!isFuture) return;
                          // aquí abres modal de edición
                        }}
                      />
                    </span>
                  </td>
                </tr>
              );
            })
          )}
        </tbody>
      </Table>
    </div>
  );
}
